


  /* 0. Mini SmartPhones ELEJE*/
@media (min-width: 120px) and (max-width: 319px) {

    .close_sidedrawer_ugy{
        display:none;
    }
    
    .open_sidedrawer_ugy{
        margin-top: 2%;
        font-size: 4vw;
        line-height: 1.2rem;
        text-align: justify;
        padding-top:2%;
        padding-bottom: 4%;
        max-width: 100%;
        border:1px dashed rgb(0, 225, 255);
        border-radius: 3%;
        margin-bottom:2%; 
        padding-left: 2%;
        padding-right: 2%;
       
    }
    
    .open_sidedrawer_ugy p{
        line-height: 1.5rem;
       
    }
    
    .Base_expand_egesz_ugy{
        display: flex;
        flex-direction: column;  
        align-items: center;
        /*border:3px solid red;*/
    }
    
    .Button_kontener_ugy{
        display:flex;
        justify-content: center;
        width:7rem;
        padding-bottom: 4%;
        /*border: 3px solid red;*/
      
    }
    
    .button_ugy{
        color:white;
       /* background-color: rgb(100, 250, 2);*/
        background-color: rgba(40, 164, 241, 0.938);
        border:1px solid rgba(40, 164, 241, 0.938);
        width:5rem;
        height:1.5rem;
        border-radius: 5%;
        opacity: 0.7;
        font-size: 0.9rem;
    }
    
    ul.felsorol_ugy {
        list-style-type: circle;
        margin-left:3rem;
        text-align: left;
        margin-top:2%;
       
    }
    
    li span {
        text-indent:-18px;
    }
    
    
    
    .Kep_start_ugy{    
        width: 100%;
    }
    .Kep_start_ugy img {
        width: 100%;
    }


}

