@media (min-width: 120px) and (max-width: 319px) {

    .Base_szoveg_egesz_ugy{
        display:flex;
        flex-direction: column;
        text-align: center;
       /* border: 3px solid red;*/
    }
    
    .Csomag_cim_ugy{
        color: rgba(47,218,241,.87);
        color:rgba(49,193,245);
