/* 4. Tablets, Ipads (landscape) ELEJE*/
@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
    .Base_szoveg_egesz_ugy{
        display:flex;
        flex-direction: column;
        text-align: center;
         /*border: 3px dashed red;*/
        max-height: 19rem; 
    }
    
    .Csomag_cim_ugy{
        display:flex;
        flex-direction: row;
        justify-content: center;
