        font-size: 2rem;
        font-weight: 900;
        padding:2%;    
    }

    .Csomag_cim_ugy h1{
        padding-top: 1rem;
        border: 2px solid rgba(47,218,241,.87);
        border-radius: 3%;
        width:17rem;
        height: 4rem;
    }
    
    .Csomag_ar_ugy{
        display:flex;
        flex-direction: row;
        align-items: center;
        font-size: 1.2rem;
        font-weight: 900;
        padding-left:2%;
        padding-right:2%;
        padding-top: 1rem;
      /*  border: 3px solid purple;*/
        min-height: 5rem;
        max-height:5rem;
    }
    .Akcio_ugy{
        color:red;
    }

    .Ered_ugy_ar{
        text-decoration-line: line-through;
    }
   .Csok_ugy_ar{
       color:red;
       padding-left: 2%;
   }
    
    .Csomag_leiras_ugy{
        max-width: 100%;
        margin-top: 2%;
        font-size: 0.9vw; 
        text-align: justify;
        padding-left:2%;
        padding-right:2%;
        /*padding-bottom: 4%;   */
       /* border: 3px solid yellow;*/
        min-height: 6rem;
        max-height: 6rem;
    }
    .Csomag_leiras_ugy p{
        line-height: 1.5rem;
       /* border: 3px solid yellow; */
        min-height: 6rem;
        max-height: 6rem;       
    }

 
}
